   @import "../../var.scss";

   .list {
       margin-bottom: 30px;

       &__add-button {
           opacity: .3;
           transition: all .3s;

           &:hover {
               transition: all .3s;
               opacity: .7;
           }
       }

       &-item {
           &:hover .list__remove-icon {
               opacity: 1;
           }

           display: flex;
           align-items: center;
           padding: 10px 12px;
           cursor: pointer;
           margin-bottom: 5px;
           transition: all .3s;

           &:hover {
               transition: all .3s;
               background: rgba(255, 255, 255, 0.5);
               border-radius: 4px;
           }

           &.active {
               background: $bg-color-white;
               box-shadow: 0px 2px 10px $shadow-hover;
               border-radius: 4px;
           }

           & span {
               flex: 1;
               text-overflow: ellipsis;
               overflow: hidden;
               white-space: nowrap;
           }
       }

       &__remove-icon {
           opacity: 0;
           transition: all .3s;

           &:hover {
               opacity: 1;
               transition: all .3s;
           }
       }
   }

   .icon {
       display: inline-block;
       margin-right: 10px;
   }