.tasks {
    &:not(:last-of-type) {
        margin-bottom: 40px;
    }

    &__title {
        font-family: 'Montserrat',
            sans-serif;
        font-weight: bold;
        font-size: 38px;
        line-height: 39px;
        border-bottom: 1px solid #f2f2f2;
        padding-bottom: 20px;
        color: #64c4ed;

        &:hover {
            img {
                opacity: 0.6;
            }
        }

        img {
            opacity: 0;
            cursor: pointer;
            padding-left: 15px;
            padding-bottom: 3px;

            &:hover {
                opacity: 1;
            }
        }
    }

    &__items {
        margin-top: 40px;

        h2 {
            font-family: 'Montserrat', sans-serif;
            font-weight: bold;
            font-size: 38px;
            text-align: center;
            color: #c9d1d3;
            position: absolute;
            left: 50%;
            top: 50%;
        }

        p {
            font-size: 24px;
            margin-left: 20px;
        }

        &-row {
            $row: &;
            display: flex;
            align-items: center;
            margin-bottom: 15px;

            &:hover &-actions {
                opacity: 1;
            }

            input {
                margin-left: 15px;
                border: 0;
                font-size: 18px;
                width: 100%;
            }

            &-actions {
                display: flex;
                opacity: 0;
                transition: opacity 0.15s ease-in-out;

                div {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 32px;
                    height: 32px;
                    background-color: lighten(#f4f6f8, 2%);
                    border-radius: 6px;
                    margin-left: 5px;
                    cursor: pointer;

                    &:hover {
                        background-color: darken(#f4f6f8, 2%);

                        svg {
                            opacity: 0.9;
                        }
                    }

                    svg {
                        width: 11px;
                        height: 11px;
                        opacity: 0.4;
                    }
                }
            }
        }
    }

    .checkbox {
        input {
            display: none;
        }

        input:checked+label {
            transition: all .1s;
            background-color: #4dd599;
            border-color: #4dd599;

            svg {
                opacity: 1;

                path {
                    stroke: #fff
                }
            }
        }

        &:hover {
            label {
                background-color: #f2f2f2;
                border-color: #f2f2f2;

                svg {
                    opacity: 1;

                    path {
                        stroke: #b2b2b2;
                    }
                }
            }
        }


        label {
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            border: 3px solid #e8e8e8;
            border-radius: 30px;
            width: 30px;
            height: 30px;
            transition: all .1s;

            svg {
                opacity: 0;
                position: relative;
            }
        }
    }

    &__form {
        margin-top: 20px;

        &-block {
            .btn {
                margin-right: 10px;
                margin-top: 15px;
            }

            .field {
                font-size: 18px;
                padding: 15px 15px;
            }
        }

        &-new {
            display: flex;
            align-items: center;
            cursor: pointer;
            opacity: .4;
            transition: opacity .15s ease-in-out;

            &:hover {
                opacity: 0.8;
            }

            img {
                width: 20px;
                height: 20px;
                margin-left: 4px;
            }

            span {
                font-size: 18px;
                margin-left: 19px;
            }
        }
    }
}

.swal-button--danger {
    background-color: #3dd599;
    border-radius: 4px;
    color: #fff;
    padding: 15px 25px;
    transition: all .15s ease-in-out;
}

.swal-button--danger:not([disabled]):hover {
    background-color: darken(#3dd599, 5%);
}

.swal-button--cancel {
    background-color: #f4f6f8;
    color: #444;
    padding: 15px 25px;
}

.swal-button {
    font-weight: 500;
}

.swal-button:focus {
    box-shadow: none;
}

.swal-content__input,
.swal-content__textarea:target {
    border-color: #EFEFEF;
}

.swal-content__input:focus,
.swal-content__textarea:focus {
    border-color: #c4c4c4;
}

.swal-text:first-child {
    font-weight: 700;
    font-size: 24px;
}

.swal-button--confirm {
    background-color: #3dd599;
    transition: all .15s ease-in-out;

    &:hover {
        background-color: darken(#3dd599, 5%);
    }
}

.remove-task .swal-button-container .swal-button:not([disabled]):hover {
    background-color: darken(#3dd599, 5%);
}